<template>
  <b-container fluid class="amap-page-container">
    <b-card>
      <h2>车辆智能定位跟踪报警器</h2>
    </b-card>
    <b-card title="当前位置">
      <el-amap vid="amapDemo" :amap-manager="amapManager" :zoom="zoom" :center="center" class="amap-demo">
        <el-amap-marker v-for="marker in markers" :position="marker.position">
        </el-amap-marker>
      </el-amap>
    </b-card>
    <b-card title="当前状态">
      <ul>
        <li>记录时间：{{ this.statuses.time }}</li>
        <li>动力电池电压：{{ this.statuses.power_voltage }} V</li>
        <li>备份电池电压：{{ this.statuses.backup_voltage }} V</li>
        <li v-if="this.statuses.lock_status === 1">锁状态：已上锁</li>
        <li v-else>锁状态：未上锁</li>
        <li v-if="this.statuses.alarm_status === 1">报警器状态：正在报警</li>
        <li v-else>报警器状态：未在报警</li>
        <li v-if="this.statuses.vibrate_alarm_status === 1">震动报警状态：正在报警</li>
        <li v-else>震动报警状态：未在报警</li>
        <li v-if="this.statuses.lock_mode === 1">锁模式：常开</li>
        <li v-else>锁模式：常闭</li>
        <li v-if="this.statuses.alarm_mode === 1">报警器模式：常开</li>
        <li v-else>报警器模式：常关</li>
        <li v-if="this.statuses.brushless_control_mode === 1">无刷控制器锁模式：常开</li>
        <li v-else>无刷控制器锁模式：常关</li>
      </ul>
    </b-card>
  </b-container>
</template>

<script>
  import axios from 'axios'
  // import amapManager from 'vue-amap'
  import {AMapManager} from 'vue-amap'

  let amapManager = AMapManager

  export default {
    name: 'zz-alarm',
    data() {
      return {
        locations: [],
        statuses: [],
        errors: [],
        center: [113.303786680707, 23.1205401843871],
        markers: [],
        amapManager,
        zoom: 15
      }
    },
    created() {
      let position = []
      // First: Get location data.
      axios.get('/api/location/').then(response => {
        this.locations = response.data
        position.push(parseFloat(response.data.long))
        position.push(parseFloat(response.data.lat))
        console.log(position)
        this.markers.push({
          'position': position
        })
      }).catch(e => {
        this.errors.push(e)
      })
      // Second: Get status data.
      axios.get('/api/status/').then(response => {
        this.statuses = response.data
      }).catch(e => {
        this.errors.push(e)
      })
      position = []
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .amap-demo {
    height: 300px;
  }
</style>



